<template>
    <div class="panel-default panel">

        <div class="panel-heading">
            售后管理

        </div>

        <div class="panel-body">
        <!-- 搜索控件开始 -->
        <div class="search-options">


            <table cellspacing="0" width="100%">
                <tbody>

                <tr>
                    <td>


                        订单编号:<input name="orderno" @keyup.enter="search" v-model="orderno" class="input-txt" type="text"/>


                        <button class="btn btn-danger" @click="search">
                            <i class="fa fa-search"></i>搜索
                        </button>
                    </td>
                </tr>
                </tbody>
            </table>


        </div>
        <!-- 搜索控件结束 -->

        <div class="action-details">
            <div class="btn-group">

                <span class="btn btn-success" @click="deleteRec">删除</span>
            </div>
        </div>

        <table width="100%" border="0" cellspacing="0" cellpadding="0" class="ui-record-table">
            <thead>
            <tr>
                <th>
                    <input type="checkbox" @click="selectedAllHandler" v-model="selectedAll"/>
                </th>
                <th><b>订单编号</b></th>
                <th><b>商品名</b></th>
                <th><b>数量</b></th>
                <th><b>价格(元)</b></th>
                <th><b>状态</b></th>
                <th><b>创建时间</b></th>
                <th><b>原因</b></th>
                <th>
                    操作
                </th>

            </tr>

            </thead>
            <tbody>


            <tr v-for="(shouhou,index) in  listShouhou" :key="shouhou.id">
                <td>
                    <input class="check" name="ids" type="checkbox" v-model="shouhou.rowSelected"/>
                </td>
                <td>
                    {{shouhou.orderno}}
                </td>
                <td>
                    {{shouhou.spname}}
                </td>
                <td>
                    {{shouhou.count}}
                </td>
                <td>
                    {{shouhou.price}}
                </td>
                <td>
                    <span v-if="shouhou.state==1">等待处理</span>
                    <span v-if="shouhou.state==2">同意售后</span>
                    <span v-if="shouhou.state==3">拒绝</span>
                </td>
                <td>
                    {{shouhou.createtime}}
                </td>
                <td>
                    {{shouhou.reason}}
                </td>
                <td>
                    <div class="el-button-group">
                        <pop-dialog-button title="售后审核" @refresh="search" v-if="shouhou.state==1" :url="'/admin/shouhouspnext?id='+shouhou.id" css-class="el-button el-button--danger el-button--small"><i class="el-icon-arrow-right"></i>审核</pop-dialog-button>
                        <pop-dialog-button title="售后详情" :url="'/admin/shouhoudetails?id='+shouhou.id" css-class="el-button el-button--warning el-button--small">详细</pop-dialog-button>
                    </div>
                </td>
            </tr>

            <tr v-if="listShouhou.length==0">
                <td colspan="20">
                    没有相关售后信息
                </td>
            </tr>
            </tbody>
        </table>
        <el-pagination
            @size-change="pagesizeChange"
            @current-change="pageindexChange"
            :current-page="pageindex"
            :page-sizes="[pagesize]"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        </div>
    </div>
</template>

<script>
    import {VueUtil} from "../util/vueutil";
    import PopDialogButton from "../common/mydialog/PopDialogButton";

    export default {
        name: "shouhoumanager",
        components: {PopDialogButton},
        data() {
            return {
                "orderno": "",
                hostHead: this.$app.contextPath,
                selectedAll: false,
                listShouhou: [],
                pageindex: 1, //初始页
                pagesize: 10,
                total: 10
            }

        },
        methods: {
            pagesizeChange: function (pagesize) {
                this.pagesize = pagesize;
            },
            pageindexChange: function (pageindex) {
                this.pageindex = pageindex;
                console.log(this.pageindex);
                this.search();
            },
            async search() {
                let url = "admin/shouhou/list";
                let param = {
                    currentpageindex: this.pageindex,
                    pagesize: this.pagesize,
                    "orderno": this.orderno,
                };
                let util = new VueUtil(this);
                console.log("this.pageindex=" + this.pageindex);
                let res = await util.http.post(url, param);
                if (res.data != null) {
                    let pageInfo = res.data.data;
                    this.total = pageInfo.total;
                    this.listShouhou = pageInfo.list;
                    console.log(pageInfo);
                }
            },
            async deleteRec() {
                let url = "admin/shouhou/delete";
                let util = new VueUtil(this);
                let hasChecked = this.listShouhou.some(c => {
                    return c.rowSelected == true;
                });
                if (!hasChecked) {
                    util.alert('请选择需要删除的记录', '系统提示', {
                        confirmButtonText: '确定'
                    });
                    return;
                }

                let ids = this.listShouhou.filter(c => c.rowSelected).map(c => c.id);
                let res = util.confirm('是否要删除数据?', '系统提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(() => {
                    util.http.post(url, {ids}, {emulateJSON: false}).then(res => {
                        if (res.data != null && res.data.stateCode > 0) {
                            this.search();
                        }
                    });
                });
            },
            selectedAllHandler() {
                console.log(this.selectedAll);
                if (this.listShouhou != null) {
                    this.listShouhou.forEach(c => {
                        c.rowSelected = !this.selectedAll;
                    });
                }
            }
        },
        created() {
            this.search();
        }
    }
</script>

<style scoped>

</style>
